@import "./App.scss";

.footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 4rem;

  p {
    width: 80%;
  }
  .social-link {
    font-size: 24px;
    margin: 1rem;
    .fab {
      cursor: pointer;
      margin: 0.5rem;
      color: black;
      &:hover {
        opacity: 0.5;
      }
    }
  }
  hr {
    height: 24px;
    border: none;
    float: right;
    background-color: #f9ea9a;
    width: 50px;
    margin-right: 30px;
  }
}

@media screen and (max-width: 1400px) {
  .footer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 1rem auto;
    p {
      width: 20%;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-right: 0rem;

    p {
      width: 100%;
      text-align: center;
    }
    .social-link {
      font-size: 24px;
      margin: 0rem;
    }
  }
}
