* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

$fontColor: #28262c;

.hamburger-nav {
  display: none;
}

.App {
  font-family: "Lato", sans-serif;
  width: 1440px;
  margin: 0 auto;
  height: 100%;
}

.navbar {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  list-style: none;
  margin-right: 5rem;
  a {
    text-decoration: none;
    color: $fontColor;
  }
  li {
    padding: 1rem;
  }
  .home-link {
    font-size: 24px;
    position: relative;
    .home-hr {
      position: absolute;
      height: 10px;
      border: none;
      background-color: #f9ea9a;
      width: 4rem;
      top: 2.3rem;
      z-index: -1;
      opacity: 0;
      transition: all 0.3s ease;
      transform: translateX(-100%);
    }
    .locationHome {
      opacity: 1;
      transform: translateX(0);
    }
    &:hover {
      .home-hr {
        opacity: 1;
        transition: all 0.3s ease;
        transform: translateX(0);
      }
    }
  }
  .work-link {
    font-size: 24px;
    position: relative;
    .work-hr {
      position: absolute;
      height: 10px;
      border: none;
      background-color: #f9ea9a;
      width: 4rem;
      top: 2.3rem;
      z-index: -1;
      opacity: 0;
      transition: all 0.3s ease;
      transform: translateX(-100%);
    }
    .locationWork {
      opacity: 1;
      transform: translateX(0);
    }
    &:hover {
      .work-hr {
        opacity: 1;
        transition: all 0.3s ease;
        transform: translateX(0);
      }
    }
  }
  .resume {
    font-size: 24px;
    transition: all 0.6s ease;
    border-radius: 100%;
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &:hover {
      transition: all 0.6s ease;
      background: #f9ea9a;
      &::after {
        opacity: 1;
        transition: all 0.6s ease;
      }
    }
    &::after {
      content: "Download Resume";
      font-size: 12px;
      position: absolute;
      margin-top: 5rem;
      color: gray;
      width: 50px;
      text-align: center;
      opacity: 0;
      transition: all 0.6s ease;
    }
  }
}
#hamburger {
  display: none;
}

@media screen and (max-width: 1400px) {
  .App,
  .Home {
    max-width: 768px;
  }
  .navbar {
    display: none;
  }

  #hamburger {
    display: block !important;
    font-size: 36px;
    float: right;
    padding: 1rem;
    cursor: pointer;
    z-index: 1;
    position: relative;
  }

  .hamburger-nav {
    position: absolute;
    display: flex;
    background: white;
    min-width: 768px;
    height: 33vh;
    justify-content: center;
    text-align: center;
    transform: translateY(-100%);
    transition: all 0.3s ease;
    z-index: 0;
    li {
      list-style-type: none;
      padding: 1rem;
    }
    a {
      text-decoration: none;
      color: $fontColor;
      font-size: 34px;
    }
    .resume-hamburger {
      a {
        font-size: 24px;
      }
      .fas {
        margin-left: 10px;
      }
    }
  }
  .visible-hamburger {
    transform: translateY(0%);
    transition: all 0.3s ease;
  }
}

@media screen and (max-width: 768px) {
  .App,
  .Home {
    width: 356px;
  }
  .navbar {
    display: none;
  }

  #hamburger {
    display: block !important;
    font-size: 36px;
    float: right;
    padding: 1rem;
    cursor: pointer;
    z-index: 1;
    position: relative;
  }

  .hamburger-nav {
    position: absolute;
    display: flex;
    background: white;
    width: 100%;
    height: 100vh;
    justify-content: center;
    text-align: center;
    transform: translateY(-100%);
    transition: all 0.3s ease;
    z-index: 0;
    li {
      list-style-type: none;
      padding: 1rem;
    }
    a {
      text-decoration: none;
      color: $fontColor;
      font-size: 24px;
    }
    .resume-hamburger {
      a {
        font-size: 18px;
      }
      .fas {
        margin-left: 10px;
      }
    }
  }
  .visible-hamburger {
    transform: translateY(0%);
    transition: all 0.3s ease;
  }
}
